.main-body {
  flex-direction: column;
  flex-wrap: initial;
  align-content: center;
  gap: 40px;
  padding: 30px;
  display: flex;
}

.go-back {
  color: #fff;
  border-radius: 20px;
  align-self: flex-start;
  font-weight: 800;
  text-decoration: none;
  transition: all .2s ease-in-out;
}

.go-back:hover {
  transform: scale(1.1);
}

.heading {
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  display: grid;
}

.pl-title {
  color: #fff;
  place-self: center;
}

.fa {
  color: #fff;
  cursor: pointer;
  transform: scale(1.1);
}

#song-name {
  color: #fff;
  max-height: 20px;
  overflow: scroll;
}

#singer-name {
  color: gray;
  max-height: 20px;
  overflow: scroll;
}

.song-img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 5px;
  align-self: center;
}

.fa-heart-o {
  color: #fff;
  cursor: pointer;
  transform: scale(1.4);
}

.fa-heart {
  color: green;
  cursor: pointer;
  transform: scale(1.4);
}

.name-like {
  place-content: center space-between;
  padding-right: 10px;
  display: flex;
}

.song-info {
  flex-direction: column;
  gap: 7px;
  display: flex;
}

.like-icon {
  align-self: center;
}

.rotate {
  transform: rotate(270deg);
}

.fa-repeat {
  transition: all .6s ease-in-out;
}

.control-buttons {
  justify-content: space-between;
  margin-top: 10px;
  display: flex;
}

.controls {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.main-controls {
  gap: 20px;
  display: flex;
}

.fa-backward, .fa-forward {
  transform: scale(1.1);
}

#play-pause {
  transform: scale(1.4);
}

.shuffeled {
  color: green;
}

.song-len {
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 10px;
  display: grid;
}

#current-time, #max-time {
  color: #fff;
}

.seek_slider {
  color: #000;
  background-color: #0000;
}

input[type="range"] {
  height: 25px;
  -webkit-appearance: none;
  width: 100%;
  margin: 10px 0;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: #fff;
  border: 0 solid #000;
  border-radius: 1px;
  box-shadow: 0 0 #000;
}

input[type="range"]::-webkit-slider-thumb {
  height: 10px;
  width: 10px;
  cursor: pointer;
  -webkit-appearance: none;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 25px;
  margin-top: -3px;
  box-shadow: 0 0 #000;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #fff;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  background: #fff;
  border: 0 solid #000;
  border-radius: 1px;
  box-shadow: 0 0 #000;
}

input[type="range"]::-moz-range-thumb {
  height: 18px;
  width: 18px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 25px;
  box-shadow: 0 0 #000;
}

input[type="range"]::-ms-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  color: #0000;
  background: none;
  border-color: #0000;
}

input[type="range"]::-ms-fill-lower {
  background: #fff;
  border: 0 solid #000;
  border-radius: 2px;
  box-shadow: 0 0 #000;
}

input[type="range"]::-ms-fill-upper {
  background: #fff;
  border: 0 solid #000;
  border-radius: 2px;
  box-shadow: 0 0 #000;
}

input[type="range"]::-ms-thumb {
  height: 2px;
  width: 2px;
  cursor: pointer;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 25px;
  margin-top: 1px;
  box-shadow: 0 0 #000;
}

input[type="range"]:focus::-ms-fill-lower {
  background: #fff;
}

input[type="range"]:focus::-ms-fill-upper {
  background: #fff;
}

