.main-body{
    display: flex;
    flex-direction: column;
    flex-wrap: initial;
    align-content: center;
    padding: 30px;
    gap: 40px;
}

.go-back{
    text-decoration: none;
    color: white;
    font-weight: 800;
    border-radius: 20px;
    align-self: flex-start;
    transition: all 0.2s ease-in-out;
}

.go-back:hover{
    transform: scale(1.1);
}

.heading{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    align-items: center;
}

.pl-title{
    color: white;
    align-self: center;
    justify-self: center;
}

.fa{
    color: white;
    transform: scale(1.1);
    cursor: pointer;
}

#song-name{
    color: white;
    overflow: scroll;
    max-height: 20px;
}

#singer-name{
    color: gray;
    overflow: scroll;
    max-height: 20px;
}

.song-img{
    width: 300px;
    height: 300px;
    object-fit: cover;
    align-self: center;
    border-radius: 5px;
}

.fa-heart-o{
    color: white;
    transform: scale(1.4);
    cursor: pointer;
}

.fa-heart {
    color: green;
    transform: scale(1.4);
    cursor: pointer;
}

.name-like{
    display: flex;
    align-content: center;
    justify-content: space-between;
    padding-right: 10px;
}

.song-info{
    display: flex;
    flex-direction: column;
    gap: 7px;
}

.like-icon{
    align-self: center;
}

.rotate{
    transform: rotate(270deg);
}

.fa-repeat{
    transition: all 0.6s ease-in-out;
}

.control-buttons{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.controls{
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.main-controls{
    display: flex;
    gap: 20px;
}

.fa-backward, .fa-forward{
    transform: scale(1.1);
}

#play-pause{
    transform: scale(1.4);
}

.shuffeled{
    color: green;
}

.song-len{
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    align-items: center;
}

#current-time,
#max-time{
    color: white;
}

.seek_slider{
    background-color: transparent;
    color: black;
}

input[type=range] {
    height: 25px;
    -webkit-appearance: none;
    margin: 10px 0;
    width: 100%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000;
    background: #ffffff;
    border-radius: 1px;
    border: 0px solid #000000;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #ffffff;
    height: 10px;
    width: 10px;
    border-radius: 25px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -3px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #ffffff;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000;
    background: #ffffff;
    border-radius: 1px;
    border: 0px solid #000000;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #ffffff;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 2px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #ffffff;
    border: 0px solid #000000;
    border-radius: 2px;
    box-shadow: 0px 0px 0px #000000;
  }
  input[type=range]::-ms-fill-upper {
    background: #ffffff;
    border: 0px solid #000000;
    border-radius: 2px;
    box-shadow: 0px 0px 0px #000000;
  }
  input[type=range]::-ms-thumb {
    margin-top: 1px;
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #ffffff;
    height: 2px;
    width: 2px;
    border-radius: 25px;
    background: #ffffff;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #ffffff;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #ffffff;
  }
